import React from 'react';
import s from './Post.module.css';
type PropsType = {
  message: string;
  likesCount: number;
};
const Post: React.FC<PropsType> = (props) => {
  return (
    <div className={s.item}>
      <img src="data:image/jpeg;base64," />
      <span>{props.message}</span>
      <div>
        <span>like</span>
        {props.likesCount}
      </div>
    </div>
  );
};

export default Post;
